"use client";

import { useState, useEffect } from "react";

export default function Dashboard() {
  const [tasks, setTasks] = useState([]);
  const [newTask, setNewTask] = useState({ title: "", description: "", points: 10 });

  useEffect(() => {
    const fetchData = async () => {
      const taskRes = await fetch("/api/tasks");
      const tasksData = await taskRes.json();
      setTasks(tasksData);
    };
    fetchData();
  }, []);

  const addTask = async () => {
    if (!newTask.title) return;
    const res = await fetch("/api/tasks", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify(newTask),
    });
    const task = await res.json();
    setTasks([...tasks, task]);
    setNewTask({ title: "", description: "", points: 10 });
  };

  return (
    <div className="flex flex-col items-center p-5">
      <h1 className="text-2xl font-bold">Your Tasks</h1>

      {/* ✅ Task Form */}
      <div className="flex flex-col gap-2 mt-4 w-1/2">
        <input
          type="text"
          placeholder="Task Name"
          value={newTask.title}
          onChange={(e) => setNewTask({ ...newTask, title: e.target.value })}
          className="border p-2 rounded"
        />
        <textarea
          placeholder="Task Description"
          value={newTask.description}
          onChange={(e) => setNewTask({ ...newTask, description: e.target.value })}
          className="border p-2 rounded"
        />
        <input
          type="number"
          placeholder="Points"
          value={newTask.points}
          onChange={(e) => setNewTask({ ...newTask, points: Number(e.target.value) })}
          className="border p-2 rounded"
        />
        <button onClick={addTask} className="bg-green-500 text-white p-2 rounded">
          Add Task
        </button>
      </div>

      {/* ✅ Display Task List */}
      <ul className="mt-4 w-1/2">
        {tasks.map((task) => (
          <li key={task.id} className="flex flex-col p-2 border-b">
            <span className="text-lg font-semibold">{task.title}</span>
            <span className="text-sm text-gray-600">{task.description}</span>
            <span className="text-sm font-bold">{task.points} pts</span>
          </li>
        ))}
      </ul>
    </div>
  );
}
